import React from 'react'
import Img from 'gatsby-image'
import { Link, graphql } from 'gatsby'

import Layout from '../components/layout'
import SEO from '../components/seo'

const SecondPage = ({ data }) => (
  <Layout>
    <SEO title="Page two" />
    <h1>Hi from the second page</h1>
    <p>Welcome to page 2</p>
    <Link to="/">Go back to the homepage</Link>
    <Img sizes={data.cgImage.childImageSharp.sizes}  style={{ width: "100%" }} alt={this.props.desc} />
  </Layout>
)

export const portfolioFragment = graphql`
fragment portfolioFragment on File {
      childImageSharp {
        sizes(maxWidth: 1000) {
          ...GatsbyImageSharpSizes
        }
      }
}
`;

export const query = graphql`
  query portfolioQuery {
    cgImage:file(relativePath: { eq: "gatsby-astronaut.png" }) {
      ...portfolioFragment
    }
  }
`;

export default SecondPage
